.fadeinRight{
    -webkit-animation: fadeinRight 1.6s both;
            animation: fadeinRight 1.6s both;
}
.fadeinTop{
    -webkit-animation: fadeinTop 0.5s both;
            animation: fadeinTop 0.5s both;
}


@-webkit-keyframes fadeinTop{
    0%{
     opacity:0;
     -webkit-transform:translateY(-50px);
             transform:translateY(-50px)
    }100%{
     opacity:1;
     -webkit-transform:translateY(0);
             transform:translateY(0)
    }
}


@keyframes fadeinTop{
    0%{
     opacity:0;
     -webkit-transform:translateY(-50px);
             transform:translateY(-50px)
    }100%{
     opacity:1;
     -webkit-transform:translateY(0);
             transform:translateY(0)
    }
}
@-webkit-keyframes fadeinRight{
    0%{
     opacity:0;
     -webkit-transform:translateX(50px);
             transform:translateX(50px)
    }100%{
     opacity:1;
     -webkit-transform:translateX(0);
             transform:translateX(0)
    }
}
@keyframes fadeinRight{
    0%{
     opacity:0;
     -webkit-transform:translateX(50px);
             transform:translateX(50px)
    }100%{
     opacity:1;
     -webkit-transform:translateX(0);
             transform:translateX(0)
    }
}
.fadeinLeft{
    -webkit-animation: fadeinLeft 1.6s both;
            animation: fadeinLeft 1.6s both;
}
@-webkit-keyframes fadeinLeft{
